        methods: {

            resetTimeDisable(data, type) { //重置时间disabled
                    let dataTime = data;
                    this.timeContrast(dataTime, 'cStartTime', 'timeDisable')
                },
                resetEndTimeDisable(data, type) { //重置结束时间disabled
                    let dataTime = data;
                    this.timeContrast(dataTime, 'cEndTime', 'timeEndDisable')
                },
                timeContrast(data, timeCan, disableCan) {
                    if (new Date(data) > new Date()) {
                        this[disableCan] = -1;
                        //this[timeCan] = this.cTimeList[0];
                    } else {
                        let hours = new Date().getHours();
                        this[disableCan] = hours;
                        if (timeCan == 'cStartTime') {
                            //this[timeCan] = this.cTimeList[hours + 1];
                        } else {
                            //this[timeCan] = "23:59";
                        }
                    }
                },
                beforeLoad: function(deliverId, flag) {


                    //清空
                    this.orderId = "";
                    this.deliverName = "";
                    this.targetCodeKeys = [];
                    this.timeType = "0";
                    this.cStartDate = "";
                    this.cEndDate = "";
                    this.cStartTime = "00:00";
                    this.cEndTime = "23:59";
                    this.totalUnContinuousTime = "0";
                    this.open = false;
                    this.unContinuousTimevalue = "";
                    this.unContinuousDateList = "";
                    this.unContinuousDateListJson = [];
                    this.materialData = []
                    this.levelId = 3;
                    this.orderList = [];
                    this.codeList = [];
                    this.deliverId = deliverId;
                    this.getOrderList();
                    this.getCodeList();

                    this.modal = true;
                    this.totalWeightNum = '5';
                    this.deliverMaterialMethod = '1';
                    this.getDeilver(deliverId);


                },

                changeStatus: function(status) {
