        created() {


        },
        methods: {


            beforeLoad: function(deliverId, flag) {


                //清空
                this.orderId = "";
                this.deliverName = "";
                this.targetCodeKeys = [];
                this.timeType = "0";
                this.cStartDate = "";
                this.cEndDate = "";
                this.cStartTime = "00:00";
                this.cEndTime = "23:59";
                this.totalUnContinuousTime = "0";
                this.open = false;
                this.unContinuousTimevalue = "";
                this.unContinuousDateList = "";
                this.unContinuousDateListJson = [];
                this.materialData = []
                this.levelId = 3;
                this.orderList = [];
                this.codeList = [];
                this.deliverId = deliverId;
                this.soltNameLen = "最多可输入100个字符";
                this.isAtive = false;
                this.getOrderList();
                this.getCodeList();
                this.totalWeightNum = '5';
                this.deliverMaterialMethod = '1';
                this.modal = true;
                this.getDeilver(deliverId);

            },

            changeStatus: function(status) {
                if (status == 1) {
                    this.isTrue = true;
                } else {
                    this.isTrue = false;
                }
            },
            cancel() {
                this.soltNameLen = "最多可输入100个字符";
                this.isAtive = false;
                this.modal = false;
            },
            errorTip(orderId, orderList) { //已经删除tip
                if (!orderId) return false;
                let tipList = orderList.filter((item) => {
                    return item.fxAdOrderId == orderId
                });
                if (tipList[0].orderName.indexOf("已删除") == -1) {
                    this.errorName = '';
                } else {
                    this.errorName = '该订单已经删除';
                }
            },
            //获取投放id
            getDeilver(deliverId) {
                //AA120B2AB086E161011BDB8F705826BA
                let url = "/deliver/getDeliverById.json?deliverId=" + deliverId;
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        console.log(res.body.data);
                        let item = res.body.data;
                        this.orderId = item.orderId;
                        this.deliverName = item.deliverName;
                        let codeList = item.fxadDeliverCodeList;

                        this.totalWeightNum = item.totalWeightNum;
                        this.deliverMaterialMethod = item.deliverMaterialMethod;

                        if (this.deliverMaterialMethod == 1) {
                            this.isTrue = true;
                        } else {
                            this.isTrue = false;
                        }

                        if (item.orderStatus == 0) {
                            this.errorName = '该订单已经删除'
                        } else {
                            this.errorName = "";
                        }

                        codeList.forEach((i) => {
                            this.targetCodeKeys.push(i.id);
                        });


                        let materialList = item.fxadDeliverMaterialList;

                        materialList.forEach((i) => {
                            let j = {};

                            j["materialId"] = i["id"];
                            j["materialStatus"] = i["materialStatus"];
                            if (i.materialStatus == 0) {
                                j["materialName"] = '(已删除)' + i["materialName"];
                            } else {
                                j["materialName"] = i["materialName"];
                            }
                            j["weight"] = i["weightNum"];
                            j["isNative"] = i["isNative"];
                            j["sourceFrom"] = i["sourceFrom"];
                            this.materialData.push(j);

                        });
                        console.log("=================");
                        console.log(this.materialData);


                        this.timeType = "" + item["dateType"] + "";
                        this.levelId = item.level;
                        console.log(this.timeType);

                        let dateList = item.deliverTimeList;
                        if (item["dateType"] == 0) {
                            //连续时间处理
                            let dc = dateList[0];
                            let dc_st = dc["startTime"].split(" ");
                            let dc_et = dc["endTime"].split(" ");


                            this.cStartDate = dc_st[0];
                            this.cEndDate = dc_et[0];
                            this.cStartTime = dc_st[1].substr(0, dc_st[1].lastIndexOf(":"));
                            this.cEndTime = dc_et[1].substr(0, dc_et[1].lastIndexOf(":"));

                        }
                        if (item["dateType"] == 1) {
                            this.setDateValue(dateList);


                            //不连续时间处理
                        }
                        console.log(dateList);


                        // this.orderList=res.body.data.customList;
                    }
                })
            },
            setDateValue: function(dateList) {
                let allDate = "";
                dateList.forEach((item) => {
                    let s = item.startTime.split(" ")[0];
                    let e = item.endTime.split(" ")[0];
                    let st = new Date(Date.parse(s.replace(/-/g, "/"))).getTime();
                    let et = new Date(Date.parse(e.replace(/-/g, "/"))).getTime();
                    let loop = true;
                    while (loop) {
                        allDate += "," + s;
                        st += 24 * 60 * 60 * 1000;
                        s = new Date(st).Format('yyyy-MM-dd');
                        loop = ((et - st) >= 0);
                    }

                });

                this.changeDateList(allDate.substr(1));
            },

            updateMaterial: function() { //
                let orderId = this.orderId;
                let targetCodeKeysStr = Array.from(this.targetCodeKeys).join(",");
                let deliverName = this.deliverName;
                let timeType = this.timeType;
                let deliverTimesJson = [];
                if (timeType == "0") {
                    if (this.cStartDate == "") {
                        this.$Message.error("请选择开始时间");
                        return;
                    }
                    if (this.cEndDate == "") {
                        this.$Message.error("请选择结束时间");
                        return;
                    }
                    let st = this.cStartTime + ":00";
                    let et = this.cEndTime + ":00";
                    if (this.cStartTime == "23:59") {
                        st = this.cStartTime + ":59";
                    }
                    if (this.cEndTime == "23:59") {
                        et = this.cEndTime + ":59";
                    }
                    let startDate = "" + this.cStartDate.Format('yyyy-MM-dd') + " " + st;
                    let endDate = "" + this.cEndDate.Format('yyyy-MM-dd') + " " + et;

                    let s = new Date(Date.parse(startDate.replace(/-/g, "/"))).getTime();
                    let e = new Date(Date.parse(endDate.replace(/-/g, "/"))).getTime();
                    if (s < new Date().getTime()) {
                        this.$Message.error("开始时间不能小于当前时间");
                        return;
                    }
                    if (s > e) {
                        this.$Message.error("开始时间不能大于结束时间");
                        return;
                    }


                    let m = {};
                    m["startTime"] = startDate;
                    m["endTime"] = endDate;


                    // let m={"startTime":startDate,"endTime":endDate};
                    deliverTimesJson.push(m);
                }
                if (timeType == "1") {
                    deliverTimesJson = Array.from(this.unContinuousDateListJson);
                }
                let level = this.levelId;
                let materialJson = [];
                this.materialData.forEach((item) => {
                    let n = {};
                    n["materialId"] = item["materialId"];
                    n["weight"] = item["weight"];
                    // let n={"materialId":item["materialId"],"weight":item["weight"]};
                    materialJson.push(n);
                });

                let isPass = this.validateSubmit(orderId, targetCodeKeysStr, deliverName, timeType, deliverTimesJson, level, materialJson);
                if (!isPass) {
                    return;
                }


                let url = "/deliver/createDeliver.json?isNative=0&orderId=" + orderId + "&deliverName=" + encodeURIComponent(deliverName)
                  + "&codeIds=" + targetCodeKeysStr + "&dateType=" + timeType + "&deliverTimesJson=" + encodeURIComponent(JSON.stringify(deliverTimesJson))
                  + "&totalWeightNum=" + this.totalWeightNum + "&deliverMaterialMethod=" + this.deliverMaterialMethod + "&level=" + level
                  + "&materialJson=" + encodeURIComponent(JSON.stringify(materialJson));

                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        this.modal = false;

                        this.$Message.success("复制新建成功");

                        this.$emit("filterParent");

                    }
                });


            },

            renderCode: function(item) {
                return item.label


            },

            newNoNativeMaterial() {
                this.$refs.newNoNative.beforeLoad();
                this.$refs.newNoNative.initList();
            },
            getOrderList: function() {
                //AA120B2AB086E161011BDB8F705826BA
                let url = "/order/getAllOrderList.json?status=-99";
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {
                        this.orderList = res.body.data.customList;

                        this.orderList.map(item => {
                            if (item.orderStatus == 0) {
                                item.orderName = '(已删除订单)' + item.orderName
                            }
                            return item;
                        });
                    }

                })
            },
            getCodeList: function() {
                // let url = "/code/getCodeList.json?status=-99&isNative=0&pageNo=" + this.codePageNo + "&pageSize=" + this.codePageSize;
                let url = "/common/getAllCode.json?status=-99&isNative=0";
                this.$Http.get(url, {}).then(res => {
                    if (res.body.status == 0) {

                        let list = res.body.data.list;

                        if (list && list.length > 0) {
                            for (var item of list) {
                                var data = {};
                                data["key"] = item.id;
                                data["label"] = item.codeName;
                                if (item.codeStatus == 0) {
                                    data["label"] = '(已删除)' + item.codeName
                                    data["disabled"] = false
                                } else if (item.codeStatus == 2) {
                                    data["label"] = '(已暂停)' + item.codeName
                                    data["disabled"] = false
                                } else {
                                    data["label"] = item.codeName
                                    data["disabled"] = false
                                }
                                this.codeList.push(data);
                            }
                        } else {
                            this.isLoadMore = false;
                        }


                        console.log(this.codeList);

                    }


                })

            },
            handleCodeChange: function(newTargetKeys) {
                this.targetCodeKeys = newTargetKeys;
                console.log(this.targetCodeKeys);
                // console.log(Array.from(this.targetCodeKeys).join(","));
            },

            loadMoreCode: function() { //加载更多广告位
                this.codePageNo++;

                this.getCodeList();


            },
            handleClick() {
                this.open = !this.open;
            },
            handleChange(date) {
                this.unContinuousTimevalue = date;
                this.changeDateList(date);
            },
            handleClear() {
                this.open = false;
            },
            handleOk() {
                this.open = false;
            },
            changeDateList: function(unContinuousTimevalue) {
                this.unContinuousTimevalue = unContinuousTimevalue;
                let d = [];
                if (unContinuousTimevalue != "") {
                    d = unContinuousTimevalue.split(",");
                    d.sort();
                }

                this.totalUnContinuousTime = d.length;
                let s = [];
                this.unContinuousDateListJson = [];
                let firstValue = "";
                let currentValue = "";
                let currentValueStr = "";

                d.forEach((item, index) => {
                    if (index == 0) {
                        if (index == (d.length - 1)) {
                            s.push(item);
                        }
                        firstValue = item;
                        currentValue = item;
                        currentValueStr = item;

                    } else {
                        let next = (new Date(Date.parse(currentValue.replace(/-/g, "/"))).getTime()) + 24 * 60 * 60 * 1000;

                        let itemCurrent = new Date(Date.parse(item.replace(/-/g, "/"))).getTime();
                        if (next == itemCurrent) {

                            //判断最后一个值

                            if (index == (d.length - 1)) {
                                let cjson = {};
                                cjson["startTime"] = currentValueStr + " 00:00:00";
                                cjson["endTime"] = item + " 23:59:59";
                                // let cjson={"startTime":currentValueStr+" 00:00:00","endTime":item+" 23:59:59"};
                                currentValueStr = currentValueStr + "至" + item;
                                s.push(currentValueStr);
                                this.unContinuousDateListJson.push(cjson)

                            }
                            currentValue = item;


                        } else {
                            if (firstValue == currentValue) {
                                let cjson = {};
                                cjson["startTime"] = currentValueStr + " 00:00:00";
                                cjson["endTime"] = currentValueStr + " 23:59:59";
                                // let cjson={"startTime":currentValueStr+" 00:00:00","endTime":currentValueStr+" 23:59:59"};
                                s.push(currentValueStr);
                                this.unContinuousDateListJson.push(cjson);
                            } else {
                                let cjson = {};
                                cjson["startTime"] = currentValueStr + " 00:00:00";
                                cjson["endTime"] = currentValue + " 23:59:59";
                                // let cjson={"startTime":currentValueStr+" 00:00:00","endTime":currentValue+" 23:59:59"};
                                currentValueStr = currentValueStr + "至" + currentValue;
                                s.push(currentValueStr);
                                this.unContinuousDateListJson.push(cjson)
                            }
                            if (index == (d.length - 1)) {
                                let cjson = {};
                                cjson["startTime"] = item + " 00:00:00";
                                cjson["endTime"] = item + " 23:59:59";
                                // let cjson={"startTime":item+" 00:00:00","endTime":item+" 23:59:59"};
                                s.push(item);
                                this.unContinuousDateListJson.push(cjson)

                            }

                            currentValue = item;
                            firstValue = item;
                            currentValueStr = item;

                        }

                    }

                });
                console.log(s);
                this.unContinuousDateList = s.join("\n");


            },
            deleteUnContinuousTime: function() {
                this.changeDateList("");

            },
            selectFromMaterial: function() {
                console.log(this.materialData);
                //获取物料类型
                let t = this.materialData;
                this.$refs.adMaterial.beforeLoad(t);
            },
            setMaterialData: function(selectedMaterials) {
                for (var item of selectedMaterials) {
                    let j = {};

                    j["materialId"] = item["materialId"];
                    j["materialName"] = item["materialName"];
                    j["weight"] = 5;
                    let isInclude = this.materialData.find((item) => item["materialId"] == j["materialId"]);
                    console.log(isInclude);
                    if (isInclude == undefined) {
                        this.materialData.push(j);
                    }
                }

            },

            addMaterialData: function(obj) {

                let j = {};
                j["materialId"] = obj["materialId"];
                j["materialName"] = obj["materialName"];
                j["weight"] = 5;
                let isInclude = this.materialData.find((item) => item["materialId"] == j["materialId"]);
                console.log(isInclude);
                if (isInclude == undefined) {
                    this.materialData.push(j);
                    //this.materialData2.push(j);
                }


            },

            changeMaterialData: function(obj) {
                let j = {};
                j["materialId"] = obj["materialId"];
                j["materialName"] = obj["materialName"];
                this.materialData.map((item) => {
                    if (item["materialId"] == j["materialId"]) {
                        item["materialName"] = j["materialName"]
                    }
                });
            },

            validateSubmit: function(orderId, targetCodeKeysStr, deliverName, timeType, deliverTimesJson, level, materialJson) {
                if (orderId == "") {
                    this.$Message.error("请选择订单");
                    return false;
                }
                if (deliverName == "") {
                    this.$Message.error("请输入广告名称");
                    return false;
                }
                if (targetCodeKeysStr == 0 || targetCodeKeysStr == '' || targetCodeKeysStr == null) {
                    this.$Message.error("请选择广告位");
                    return false;
                }

                if (timeType != "0" && timeType != "1") {
                    this.$Message.error("请选择时间连续或非连续时间");
                    return false;
                }

                if (deliverTimesJson.length == 0) {
                    this.$Message.error("请选择正确的时间");
                    return false;
                }

                if (level == "") {
                    this.$Message.error("请选择优先级");
                    return false;
                }
                if (materialJson.length == 0) {
                    this.$Message.error("请选择物料");
                    return false;
                }
                return true;
            },
            onFocusName() {
                this.soltNameLen = "最多可输入100个字符";
                this.isAtive = false;

            },
            userDataNum() {
                //姓名位数不在0-100间
                if (this.deliverName) {
                    var reg = /^[\s\S]{1,100}$/;
                    var r = this.deliverName.match(reg);
                    if (r == null) {
                        this.soltNameLen = "长度超过100个字符"
                        this.isAtive = true;
                    }
                } else {
                    this.soltNameLen = "订单名称不能为空"
                    this.isAtive = true;
                }
            },

        },
        components: {
            adMaterial, newNoNative, updataNonative
        }
}

</script>



